.loading {
  background: #ffffff;
  display: flex;
  font-size: 50px;
  height: 100vh;
  justify-content: center;
  position: fixed;
  width: 100vh;
}

.btn_loading {
  font-size: 100px;
}

.upload_btn_wrapper {
  display: inline-block;
  margin: 10px auto;
  position: relative;
  overflow: hidden;
  text-align: center;
}

.btn_upload {
  text-align: center;
}

.file_upload {
  font-size: 100px;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
}

.icon_upload_file {
  margin-bottom: 30px;
  font-size: 150px;
}

.img_upload {
  border: 1px solid #d9d9d9;
  display: flex;
  height: 100px;
  margin: 20px auto;
  padding: 10px;
  width: 100px;
}

.mb30 {
  margin-bottom: 30px;
}

.site_layout_background {
  border-radius: 15px;
  border: 2px dashed #bfbfbf;
  background-color: #ffffff;
  display: flex;
  height: 500px;
  justify-content: center;
  letter-spacing: 4px;
  margin: 100px auto;
  width: 700px;
  text-align: center;
}

.btn_scan {
  width: 100px;
  margin: 0px auto;
}
