.site_layout_background {
  padding: 20px;
}

.dashboard {
  padding: 10px 50px;
}

.btn {
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  color: rgba(0, 0, 0, 0.65);
  display: inline-block;
  font-size: 14px;
  height: 32px;
  margin-left: 3px;
  padding: 4px 15px;
  text-align: center;
  width: 100px;
}

.box_modal {
  width: 50%;
}

.upload_btn_wrapper {
  display: inline-block;
  margin: 10px auto;
  overflow: hidden;
  position: relative;
  text-align: center;
}

.btn_upload {
  text-align: center;
}

.file_upload {
  font-size: 100px;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
}

.layout_modal {
  border: 2px dashed #bfbfbf;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  letter-spacing: 4px;
  margin: 100px auto;
  padding: 50px;
  text-align: center;
  width: 40%;
}

.icon_upload_file {
  font-size: 15rem;
  margin-bottom: 30px;
}

.btn_create_template {
  display: inline-block;
  margin: 0px auto;
}
